.w__text-with-line {
  position: relative;
  &::before {
    top: -0.5rem;
    content: '';
    position: absolute;
    background-color: $primary;
    width: 3rem;
    height: 0.4rem;
    border-radius: 2rem;
  }
}
.w__p {
  max-width: 60ch;
}
ul {
  list-style: none;
  padding-right: 0.25rem;
}
